import { Button, Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Card, CardContent, Typography, CardActions } from "@mui/material";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Company from "../../../modal/company";
import { deleteCompany } from "../../../redux/companyState";
import { store } from "../../../redux/store";
import globals from "../../../utils/global";
import ListIcon from "@mui/icons-material/List";
import EditIcon from "@mui/icons-material/Edit";
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import jwtAxios from "../../../utils/jwtAxios";
import notify, { ErrorMessage, SuccessMessage } from "../../../utils/notify";
import "./singleCompany.css";

interface SingleCompanyProps {
    companyData: Company;
}

function SingleCompany(props: SingleCompanyProps): JSX.Element {
    const [open, setOpen] = useState(false);
    const navigate = useNavigate();

    const handleClickOpen = () => {
        setOpen(true);
    }

    const handleClose = () => {
        setOpen(false);
    }

    const remomveCompany = () => {
        handleClose();
        jwtAxios.delete(globals.adminURLs.deleteCompany + props.companyData.id)
            .then((response) => {
                if (response.status == 200) {
                    store.dispatch(deleteCompany(props.companyData.id));
                    notify.success("Company " + props.companyData.name + SuccessMessage.DELETE_SUCCESSFUL);
                    navigate("/");
                }
            })
            .catch((err) => {
                console.log(err);
                notify.error(ErrorMessage.DELETE_FAILED + "company ");
            })
    };


    const getCoupons = () => {
        navigate("/company/getAllCompanyCoupons", { state: { id: props.companyData.id } });
    };

    return (
        <div className="singleCompany solidBox">
            <Card>
                <CardContent>
                    <Typography>
                        {props.companyData.id}
                    </Typography>
                    <Typography variant="h6" component="div">
                        {props.companyData.name}
                    </Typography>
                    <Typography variant="body1">
                        {props.companyData.email}
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button
                        startIcon={<ListIcon />}
                        variant="contained"
                        color="info"
                        type="submit"
                        onClick={getCoupons}>
                        Coupons
                    </Button> &nbsp;

                    {store.getState().authState.clientType == "ADMIN" &&
                        <Button
                            startIcon={<EditIcon />}
                            variant="contained"
                            color="warning"
                            onClick={() => {
                                navigate("/admin/updateCompany", { state: { id: props.companyData.id } })
                            }}>
                            Update
                        </Button>} &nbsp;

                    {(store.getState().authState.clientType === 'ADMIN') &&
                        <Button
                            startIcon={<HighlightOffIcon />}
                            variant="contained"
                            color="error"
                            type="submit"
                            onClick={handleClickOpen}>
                            Delete
                        </Button>}
                    <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description">
                        <DialogTitle id="alert-dialog-title">
                            Are you sure you want to delete this company?
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                This company will be deleted, as well as the company's purchase history
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleClose} variant="outlined">Cancel</Button> &nbsp;
                            <Button onClick={remomveCompany} variant="contained" color="error">Delete</Button>
                        </DialogActions>
                    </Dialog>
                </CardActions>
            </Card>
        </div>
    );
}

export default SingleCompany;
