import { useState, useEffect } from "react";
import Coupon from "../../../../modal/coupon";
import { downloadGuestCoupons } from "../../../../redux/guestState";
import { store } from "../../../../redux/store";
import globals from "../../../../utils/global";
import jwtAxios from "../../../../utils/jwtAxios";
import SingleCoupon from "../../../company/singleCoupon/singleCoupon";
import { useNavigate } from 'react-router-dom';
import "./mainPage.css";

function MainPage(): JSX.Element {
    const [coupons, setCoupons] = useState<Coupon[]>(store.getState().guestState.coupons);
    const navigate = useNavigate();

    useEffect(() => {
        if (store.getState().authState.clientType == "ADMIN" || store.getState().authState.clientType == "COMPANY") {
            navigate("/homePage");
        } else if (store.getState().authState.clientType != "CUSTOMER" && store.getState().guestState.coupons.length < 1) {
            jwtAxios.get<Coupon[]>(globals.generalURLs.getAllCoupons)
                .then((response) => {
                    store.dispatch(downloadGuestCoupons(response.data));
                })
                .then(() => {
                    setCoupons(store.getState().guestState.coupons);
                })
                .catch((err) => {
                    console.log(err);
                })
        }
    }, []);

    return (
        <div className="mainPage">
            <h1>Welcome !</h1>
            <h4>Enjoy our wide variety of coupons!</h4> <hr /> <br />

            {coupons.map((item) => <SingleCoupon key={item.id} couponData={item} />)}
        </div>
    );
}

export default MainPage;
