import { SyntheticEvent, useEffect, useState } from 'react';
import Customer from "../../../modal/customer";
import { useNavigate } from 'react-router-dom';
import { store } from "../../../redux/store";
import notify, { ErrorMessage } from "../../../utils/notify";
import SingleCustomer from './../../customer/singleCustomer/singleCustomer';
import { TextField, Button } from "@mui/material";
import "./getAllCustomers.css";

function GetAllCustomers(): JSX.Element {
    const [customers, setCustomers] = useState<Customer[]>([]);
    const [search, setSearch] = useState("");
    const navigate = useNavigate();

    useEffect(() => {
        if (store.getState().authState.clientType !== "ADMIN") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
        } else {
            setCustomers(store.getState().customerState.customers);
        }
    }, []);

    const handleSearch = (args: SyntheticEvent) => {
        const value = (args.target as HTMLInputElement).value;
        setSearch(value);
    };

    return (
        <div className="getAllCustomers">
            <h1>All Customers</h1>
            <form onReset={() => { setSearch("") }}>
                <TextField placeholder="Search Customer" onChange={handleSearch} /> &nbsp;
                <Button type="reset" variant="contained">Clear</Button>
            </form> <hr />
            {customers.filter(item =>
                (item.firstName + " " + item.lastName).includes(search) ||
                (item.firstName + " " + item.lastName).toLowerCase().includes(search) ||
                item.email.toLowerCase().includes(search))
                .map((item) => <SingleCustomer key={item.id} customerData={item} />)}
        </div>
    );
}

export default GetAllCustomers;
