import { useState } from "react";
import Coupon from "../../../modal/coupon";
import { deleteCoupon } from "../../../redux/couponState";
import { store } from "../../../redux/store";
import globals from "../../../utils/global";
import jwtAxios from "../../../utils/jwtAxios";
import notify, { ErrorMessage, SuccessMessage } from "../../../utils/notify";
import { useNavigate } from 'react-router-dom';
import { Card, CardHeader, Avatar, CardMedia, CardContent, Typography, CardActions, Button, Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions } from "@mui/material";
import { blue } from "@mui/material/colors";
import EditIcon from '@mui/icons-material/Edit';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import "./singleCoupon.css";
import { purchaseCoupon } from "../../../redux/customerState";
import { updateCouponAmount } from "../../../redux/guestState";

interface SingleCouponProps {
    couponData: Coupon;
}

function SingleCoupon(props: SingleCouponProps): JSX.Element {
    const [open, setOpen] = useState(false);
    const [newAmount, setAmount] = useState(props.couponData.amount);
    const navigate = useNavigate();

    const updateAmount = () => {
        setAmount(newAmount - 1);
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const purchase = () => {
        if (store.getState().authState.clientType != "CUSTOMER") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
            return;
        }

        if (newAmount < 1) {
            notify.error(ErrorMessage.COUPON_OUT_OF_STOCK);
            return;
        }

        let customerCoupons = store.getState().customerState.customers[0].coupons;
        if (JSON.stringify(customerCoupons.find(item => item.id == props.couponData.id)) == (JSON.stringify(props.couponData))) {
            notify.error(ErrorMessage.COUPON_ALREADY_OWNED);
            return;
        }

        jwtAxios.post(globals.customerURLs.purchaseCoupon + props.couponData.id)
            .then((response => {
                if (response.status == 200) {
                    updateAmount();
                    props.couponData.amount = newAmount;
                }
            }))
            .then(() => {
                store.dispatch(updateCouponAmount(props.couponData));
            })
            .then(() => {
                store.dispatch(purchaseCoupon(props.couponData));
            })
            .then(() => {
                notify.success(SuccessMessage.PURCHASE_SUCCESSFUL);
                navigate("/homePage");
            })
            .catch((err) => {
                console.log(err);
                notify.error("ERROR");
            })
    };

    const removeCoupon = () => {
        handleClose();
        jwtAxios.delete(globals.companyURLs.deleteCoupon + props.couponData.id)
            .then((response) => {
                if (response.status == 200) {
                    store.dispatch(deleteCoupon(props.couponData.id));
                    notify.success("Coupon #" + props.couponData.id + " " + SuccessMessage.DELETE_SUCCESSFUL);
                    navigate("/homePage");
                }
            })
            .catch((err) => {
                console.log(err);
            })
    }

    const purchaseButton = (): boolean => {
        if (store.getState().authState.clientType != "CUSTOMER" && store.getState().authState.clientType != "") {
            return false;
        }

        if (newAmount < 1) {
            return false;
        }

        if (window.location.href.includes("/customer/getCustomerCoupons")) {
            return false;
        }
        return true;
    };

    return (
        <div className="singleCoupon solidBox">
            <Card>
                <CardHeader
                    avatar={
                        <Avatar sx={{ bgcolor: blue[500] }}>
                            {props.couponData.id}
                        </Avatar>
                    }
                    title={props.couponData.category}
                />
                <CardMedia
                    component="img"
                    height={"150px"}
                    width={"100px"}
                    image={props.couponData.image}
                />
                <CardContent>
                    <Typography variant="h6" component="div">
                        {props.couponData.title}
                    </Typography>
                    <Typography variant="body1" color="text.secondary">
                        {props.couponData.description}
                    </Typography>
                    {(newAmount <= 3 && newAmount > 0) && <Typography variant="h6"><u>Coupon about to run out!</u></Typography>}
                    {newAmount == 0 && <Typography variant="h6" color={"error"}>Coupon ran out</Typography>}
                    <Typography variant="body1" >Price: {props.couponData.price}</Typography>
                    <Typography variant="h6" color='error' >Expires: {props.couponData.endDate}</Typography>
                </CardContent>
                <CardActions>
                    {purchaseButton() && <Button onClick={purchase} variant="contained" fullWidth>Purchase Coupon</Button>}
                    {(store.getState().authState.clientType == 'COMPANY') &&
                        <Button
                            startIcon={<EditIcon />}
                            variant="contained"
                            color="info"
                            type="submit"
                            onClick={() => {
                                navigate("/company/updateCoupon", {
                                    state: { id: props.couponData.id }
                                });
                            }}>
                            Update
                        </Button>}
                    &nbsp; &nbsp;
                    {(store.getState().authState.clientType == 'COMPANY') &&
                        <Button
                            startIcon={<HighlightOffIcon />}
                            variant="contained"
                            color="error"
                            type="submit"
                            onClick={handleClickOpen}>
                            Delete
                        </Button>}
                    <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description">
                        <DialogTitle id="alert-dialog-title">
                            {"Are you sure you want to delete this coupon?"}
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                Delete this coupon will delete all Customers's coupons History
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleClose}>Cancel</Button> &nbsp;
                            <Button onClick={removeCoupon} variant="contained" color="error">Delete</Button>
                        </DialogActions>
                    </Dialog>
                </CardActions>
            </Card>
        </div>
    );
}

export default SingleCoupon;
