import Company from './../modal/company';

export class CompanyState {
    companies: Company[] = [];
}

export enum CompanyActionType {
    ADD_COMPANY = "Add Company",
    UPDATE_COMPANY = "Update Company",
    DELETE_COMPANY = "Delete Company",
    DOWNLOAD_COMPANIES = "Download Companies",
    CLEAR_COMPANIES = "Clear Companies",
}

export interface CompanyAction {
    type: CompanyActionType,
    payload?: any;
}

export function addCompany(company: Company): CompanyAction {
    return { type: CompanyActionType.ADD_COMPANY, payload: company };
}

export function updateCompany(company: Company): CompanyAction {
    return { type: CompanyActionType.UPDATE_COMPANY, payload: company };
}

export function deleteCompany(companyID: number): CompanyAction {
    return { type: CompanyActionType.DELETE_COMPANY, payload: companyID };
}

export function downloadCompanies(companies: Company[]): CompanyAction {
    return { type: CompanyActionType.DOWNLOAD_COMPANIES, payload: companies };
}

export function clearCompanies(): CompanyAction {
    return { type: CompanyActionType.CLEAR_COMPANIES };
}

export function companyReducer(currentState: CompanyState = new CompanyState(), action: CompanyAction): CompanyState {
    let newState = { ...currentState };

    switch (action.type) {
        case CompanyActionType.ADD_COMPANY:
            const newCompanies = [...newState.companies];
            newCompanies.push(action.payload);
            newState.companies = newCompanies;
            break;

        case CompanyActionType.UPDATE_COMPANY:
            newState.companies = newState.companies.filter(item => item.id != action.payload.id);
            newState.companies.push(action.payload);
            break;

        case CompanyActionType.DELETE_COMPANY:
            newState.companies = newState.companies.filter(item => item.id != action.payload);
            break;

        case CompanyActionType.DOWNLOAD_COMPANIES:
            newState.companies = action.payload;
            break;

        case CompanyActionType.CLEAR_COMPANIES:
            newState.companies = [];
            break;
    }
    return newState;
}