import { TextField, FormControl, InputLabel, Select, MenuItem, ButtonGroup, Button, FormHelperText } from "@mui/material";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import ClientDetails from "../../../modal/clientDetails";
import Company from "../../../modal/company";
import Customer from "../../../modal/customer";
import { userLogin } from "../../../redux/authState";
import { store } from "../../../redux/store";
import globals from "../../../utils/global";
import jwtAxios from "../../../utils/jwtAxios";
import notify, { SuccessMessage, ErrorMessage } from "../../../utils/notify";
import { downloadCompanies } from './../../../redux/companyState';
import { downloadCustomers } from "../../../redux/customerState";
import { downloadCoupons } from './../../../redux/couponState';
import "./login.css";

function Login(): JSX.Element {
    const { register, handleSubmit, formState: { errors } } = useForm<ClientDetails>();
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const send = (client: ClientDetails) => {
        jwtAxios.post<ClientDetails>(globals.generalURLs.login, client)
            .then((response) => {
                notify.success(SuccessMessage.LOGIN_APPROVED);
                dispatch(userLogin(response.headers.authorization));
                switch (store.getState().authState.clientType) {
                    case "ADMIN":
                        jwtAxios.get<Company[]>(globals.adminURLs.getAllCompanies)
                            .then((response) => {
                                store.dispatch(downloadCompanies(response.data));
                            })
                            .catch((err) => {
                                notify.error("ERROR")
                                console.log(err);
                            });

                        jwtAxios.get<Customer[]>(globals.adminURLs.getAllCustomers)
                            .then((response) => {
                                store.dispatch(downloadCustomers(response.data));
                            })
                            .catch((err) => {
                                notify.error("ERROR")
                                console.log(err);
                            });
                        break;

                    case "COMPANY":
                        jwtAxios.get<Company>(globals.companyURLs.getCompanyDetails)
                            .then((response) => {
                                store.dispatch(downloadCompanies([response.data]));
                                store.dispatch(downloadCoupons(response.data.coupons));
                            })
                            .catch((err) => {
                                notify.error("ERROR")
                                console.log(err);
                            });
                        break;

                    case "CUSTOMER":
                        jwtAxios.get<Customer>(globals.customerURLs.getCustomerDetails)
                            .then((response) => {
                                store.dispatch(downloadCustomers([response.data]));
                            })
                            .catch((err) => {
                                notify.error("ERROR");
                                console.log(err);
                            });
                        break;
                }
                navigate("/homePage");
            })
            .catch((err) => {
                notify.error(ErrorMessage.LOGIN_FAILED);
            });
    };

    return (
        <div className="login">
            <h1>Login</h1> <hr />
            <form className="solidBox" onSubmit={handleSubmit(send)}>
                <TextField type={"email"} name="email" label="Email" variant="outlined" fullWidth {...register("email", {
                    required: {
                        value: true,
                        message: "Please enter a valid email",
                    },
                })}
                />
                <FormHelperText>
                    {errors.email?.message}
                </FormHelperText> <br />

                <TextField type={"password"} name="password" label="Password" variant="outlined" fullWidth {...register("password", {
                    required: {
                        value: true,
                        message: "Please enter a password",
                    },
                })}
                />
                <FormHelperText>
                    {errors.password?.message} <br />
                </FormHelperText>

                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Client Type</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        label="Client Type"
                        defaultValue={''}
                        {...register("clientType", {
                            required: {
                                value: true,
                                message: "Please select a client type",
                            }
                        })}
                    >
                        <MenuItem value={"ADMIN"}>ADMIN</MenuItem>
                        <MenuItem value={"COMPANY"}>COMPANY</MenuItem>
                        <MenuItem value={"CUSTOMER"}>CUSTOMER</MenuItem>
                    </Select>
                </FormControl>
                <FormHelperText>
                    {errors.clientType?.message}
                </FormHelperText> <br />

                <Button type="reset" color="error" variant="contained">Reset</Button> &nbsp; &nbsp;
                <Button type="submit" color="primary" variant="contained">Login</Button>
            </form>
        </div>
    );
}

export default Login;
