import { Card, CardContent, Typography, CardActions, Button, Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions } from "@mui/material";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Customer from "../../../modal/customer";
import { deleteCustomer } from "../../../redux/customerState";
import { store } from "../../../redux/store";
import globals from "../../../utils/global";
import ListIcon from "@mui/icons-material/List";
import EditIcon from "@mui/icons-material/Edit";
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import jwtAxios from "../../../utils/jwtAxios";
import notify, { ErrorMessage, SuccessMessage } from "../../../utils/notify";
import "./singleCustomer.css";

interface SingleCustomerProps {
    customerData: Customer;
}

function SingleCustomer(props: SingleCustomerProps): JSX.Element {
    const [open, setOpen] = useState(false);
    const navigate = useNavigate();

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const removeCustomer = () => {
        handleClose();
        jwtAxios.delete(globals.adminURLs.deleteCustomer + props.customerData.id)
            .then((response) => {
                if (response.status == 200) {
                    store.dispatch(deleteCustomer(props.customerData.id));
                    notify.success("Customer " + props.customerData.firstName + " " + props.customerData.lastName + SuccessMessage.DELETE_SUCCESSFUL);
                    navigate("/");
                }
            })
            .catch((err) => {
                notify.error(ErrorMessage.DELETE_FAILED + "customer");
                console.log(err);
            })
    };

    const getCoupons = () => {
        navigate("/customer/getCustomerCoupons", { state: { id: props.customerData.id }, });
    };

    return (
        <div className="singleCustomer solidBox">
            <Card>
                <CardContent>
                    <Typography>
                        {props.customerData.id}
                    </Typography>
                    <Typography variant="h6" component="div">
                        {props.customerData.firstName} {props.customerData.lastName}
                    </Typography>
                    <Typography variant="body1">
                        {props.customerData.email}
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button
                        startIcon={<ListIcon />}
                        variant="contained"
                        color="info"
                        type="submit"
                        onClick={getCoupons}>
                        Coupons
                    </Button> &nbsp;

                    {(store.getState().authState.clientType == 'ADMIN') &&
                        <Button
                            startIcon={<EditIcon />}
                            variant="contained"
                            color="warning"
                            type="submit"
                            onClick={() => {
                                navigate("/admin/updateCustomer", {
                                    state: { id: props.customerData.id },
                                });
                            }}>
                            Update
                        </Button>} &nbsp;

                    {(store.getState().authState.clientType == 'ADMIN') &&
                        <Button
                            startIcon={<HighlightOffIcon />}
                            variant="contained"
                            color="error"
                            type="submit"
                            onClick={handleClickOpen}>
                            Delete
                        </Button>}
                    <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description">
                        <DialogTitle id="alert-dialog-title">
                            {"Are you sure you want to delete this customer ?"}
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                This customer will be deleted, as well as the the customer's purchase history
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleClose} variant="outlined">Cancel</Button> &nbsp;
                            <Button onClick={removeCustomer} variant="contained" color="error">Delete</Button>
                        </DialogActions>
                    </Dialog>
                </CardActions>
            </Card>
        </div>
    );
}

export default SingleCustomer;
