import "./getCompanyDetails.css";
import { useState } from 'react';
import Company from './../../../modal/company';
import { useEffect } from 'react';
import { store } from './../../../redux/store';
import SingleCompany from './../singleCompany/singleCompany';
import notify, { ErrorMessage } from "../../../utils/notify";
import { useNavigate } from 'react-router-dom';

function GetCompanyDetails(): JSX.Element {
    const [company, setCompany] = useState<Company>(new Company());
    const navigate = useNavigate();

    useEffect(() => {
        if (store.getState().authState.clientType !== "COMPANY") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
        } else {
            setCompany(store.getState().companyState.companies[0]);
        }
    }, []);

    return (
        <div className="getCompanyDetails">
            <h1>Company Details</h1> <hr />

            {<SingleCompany key={company.id} companyData={company} />}
        </div>
    );
}

export default GetCompanyDetails;
