import "./updateCoupon.css";
import { Box, FormControl, InputLabel, Select, MenuItem, FormHelperText, TextField, Button, SelectChangeEvent } from "@mui/material";
import { useForm } from 'react-hook-form';
import Coupon from './../../../modal/coupon';
import { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import { store } from './../../../redux/store';
import notify, { ErrorMessage, SuccessMessage } from './../../../utils/notify';
import jwtAxios from './../../../utils/jwtAxios';
import globals from './../../../utils/global';
import { updateCoupon } from './../../../redux/couponState';

function UpdateCoupon(): JSX.Element {
    const { handleSubmit } = useForm<Coupon>();
    const [coupon, setCoupon] = useState<Coupon>(new Coupon());
    const navigate = useNavigate();
    const location = useLocation();
    const { id } = location.state as any;

    useEffect(() => {
        if (store.getState().authState.clientType != "COMPANY") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/");
        } else {
            setCoupon(store.getState().couponState.coupons.find(item => item.id == id));
        }
    }, [])

    const handleSelectChange = (event: SelectChangeEvent) => {
        coupon.category = event.target.value as string;
    };

    const handleChange = (args: any) => {
        const { name, value } = args.target;
        setCoupon({
            ...coupon,
            [name]: value,
        });
    };

    const send = () => {
        jwtAxios.put(globals.companyURLs.updateCoupon, coupon)
            .then((response) => {
                if (response.status == 200) {
                    store.dispatch(updateCoupon(coupon));
                    notify.success("Coupon" + SuccessMessage.UPDATE_SUCCESSFUL);
                    navigate("/homePage");
                }
            })
            .catch((err) => {
                console.log(err);
                notify.error(ErrorMessage.UPDATE_FAILED + "coupon");
            })
    };

    return (
        <div className="updateCoupon">
            <h1>Update Coupon</h1> <hr />
            <Box component="form" id="add-coupon" onSubmit={handleSubmit(send)} className="solidBox">
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Category</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        label="Category"
                        name="category"
                        value={coupon.category as string}
                        onChange={handleSelectChange}
                        required>
                        <MenuItem value={"FOOD"}>FOOD</MenuItem>
                        <MenuItem value={"ELECTRICITY"}>ELECTRICITY</MenuItem>
                        <MenuItem value={"RESTAURANT"}>RESTAURANT</MenuItem>
                        <MenuItem value={"VACATION"}>VACATION</MenuItem>
                    </Select>
                </FormControl> <br /> <br />

                <TextField type={"text"} name="title" label="Title" required fullWidth onChange={handleChange} value={coupon.title} /> <br /> <br />

                <TextField type={"text"} name="description" label="Description" placeholder="Coupon Description"
                    multiline fullWidth maxRows={3} required value={coupon.description} /> <br /> <br />

                <TextField type={"date"} name="startDate" label="Start Date" required fullWidth onChange={handleChange} value={coupon.startDate} /> <br /> <br />

                <TextField type={"date"} name="endDate" label="End Date" required fullWidth onChange={handleChange} value={coupon.endDate} /> <br /> <br />

                <TextField type={"number"} name="amount" label="Amount" required fullWidth onChange={handleChange} value={coupon.amount} /> <br /> <br />

                <TextField type={"number"} name="price" label="Price" required fullWidth onChange={handleChange} value={coupon.price} /> <br /> <br />

                <TextField type={"text"} name="image" label="Image URL" required fullWidth onChange={handleChange} value={coupon.image} /> <br /> <br />

                <Button type="submit" variant="contained" fullWidth>Update Coupon</Button> <br /> <br />
            </Box>
        </div>
    );
}

export default UpdateCoupon;
