import Customer from './../../../modal/customer';
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate, useLocation } from "react-router-dom";
import { store } from "../../../redux/store";
import notify, { ErrorMessage, SuccessMessage } from "../../../utils/notify";
import { TextField, Button } from "@mui/material";
import jwtAxios from "../../../utils/jwtAxios";
import globals from "../../../utils/global";
import { updateCustomer } from './../../../redux/customerState';
import "./updateCustomer.css";

function UpdateCustomer(): JSX.Element {
    const { handleSubmit } = useForm<Customer>();
    const [customer, setCustomer] = useState<Customer>(new Customer());
    const navigate = useNavigate();
    const location = useLocation();
    const { id } = location.state as any;

    useEffect(() => {
        if (store.getState().authState.clientType !== "ADMIN") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
        } else {
            setCustomer(store.getState().customerState.customers.find(item => item.id == id));
        }
    }, []);

    const handleChange = (args: any) => {
        const { name, value } = args.target;
        setCustomer({
            ...customer,
            [name]: value,
        });
    };

    const send = () => {
        jwtAxios.put(globals.adminURLs.updateCustomer, customer)
            .then((response) => {
                if (response.status == 200) {
                    store.dispatch(updateCustomer(customer));
                    notify.success("Customer" + SuccessMessage.UPDATE_SUCCESSFUL);
                    navigate("/homePage");
                }
            })
            .catch((err) => {
                console.log(err);
                notify.error(ErrorMessage.UPDATE_FAILED + "customer");
            })
    };

    return (
        <div className="updateCustomer">
            <h1>Update Customer</h1> <hr />
            <form className="solidBox" onSubmit={handleSubmit(send)}>
                <TextField type={"text"} name="firstName" label="First Name" variant="outlined" fullWidth onChange={handleChange} value={customer.firstName} required />
                <br /> <br />
                <TextField type={"text"} name="lastName" label="Last Name" variant="outlined" fullWidth onChange={handleChange} value={customer.lastName} required />
                <br /> <br />
                <TextField type={"email"} name="email" label="Email" variant="outlined" fullWidth onChange={handleChange} value={customer.email} required />
                <br /> <br />
                <Button type="submit" color="primary" variant="contained" fullWidth>Update Customer</Button>
            </form>
        </div>
    );
}

export default UpdateCustomer;
