import "./addCoupon.css";
import { useForm } from 'react-hook-form';
import Coupon from './../../../modal/coupon';
import { FormControl, InputLabel, Select, MenuItem, TextField, useFormControl, Box, FormHelperText, Button } from "@mui/material";
import { useEffect } from 'react';
import { store } from './../../../redux/store';
import notify, { ErrorMessage, SuccessMessage } from "../../../utils/notify";
import { useNavigate } from 'react-router-dom';
import jwtAxios from './../../../utils/jwtAxios';
import globals from './../../../utils/global';
import { addCoupon } from "../../../redux/couponState";

function AddCoupon(): JSX.Element {
    const { register, handleSubmit, formState: { errors } } = useForm<Coupon>();
    const navigate = useNavigate();
    const formControl = useFormControl();

    useEffect(() => {
        if (store.getState().authState.clientType != "COMPANY") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
        }
    }, []);

    const send = (coupon: Coupon) => {
        jwtAxios.post(globals.companyURLs.addCoupon, coupon)
            .then((response) => {
                coupon.id = response.data;
            })
            .then(() => {
                store.dispatch(addCoupon(coupon));
                notify.success("Coupon" + SuccessMessage.ADD_SUCCESSFUL);
                navigate("/homepage");
            })
            .catch((err) => {
                console.log(err);
                notify.error(ErrorMessage.ADD_FAILED + "coupon");
            })
    };

    return (
        <div className="addCoupon">
            <h1>Add Coupon</h1> <hr />
            <Box component="form" id="add-coupon" onSubmit={handleSubmit(send)} className="solidBox">
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Category</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        label="Category"
                        name="category"
                        {...register("category", {
                            required: {
                                value: true,
                                message: "Please select a category"
                            }
                        })}
                    >
                        <MenuItem value={"FOOD"}>FOOD</MenuItem>
                        <MenuItem value={"ELECTRICITY"}>ELECTRICITY</MenuItem>
                        <MenuItem value={"RESTAURANT"}>RESTAURANT</MenuItem>
                        <MenuItem value={"VACATION"}>VACATION</MenuItem>
                    </Select>
                </FormControl>
                <FormHelperText>
                    {errors.category?.message}
                </FormHelperText> <br />

                <TextField type={"text"} name="title" label="Title" fullWidth {...register("title", {
                    required: {
                        value: true,
                        message: "Please enter coupon title",
                    },
                })}
                />
                <FormHelperText>
                    {errors.title?.message}
                </FormHelperText> <br />

                <TextField type={"text"} name="description" label="Description" placeholder="Coupon Description" {...register("description", {
                    required: {
                        value: true,
                        message: "Please enter coupon description",
                    }
                })} multiline fullWidth maxRows={3}
                />

                <FormHelperText>
                    {errors.description?.message}
                </FormHelperText> <br />

                <TextField type={"date"} name="startDate" label="Start Date" fullWidth {...register("startDate", {
                    required: {
                        value: true,
                        message: "Please select start date"
                    }
                })} defaultValue={new Date().toISOString().slice(0, 10)}
                />

                <FormHelperText>
                    {errors.startDate?.message}
                </FormHelperText> <br />

                <TextField type={"date"} name="endDate" label="End Date" fullWidth {...register("endDate", {
                    required: {
                        value: true,
                        message: "Please select end date"
                    }
                })} defaultValue={new Date().toISOString().slice(0, 10)}
                />

                <FormHelperText>
                    {errors.endDate?.message}
                </FormHelperText> <br />

                <TextField type={"number"} name="amount" label="Amount" fullWidth {...register("amount", {
                    required: {
                        value: true,
                        message: "Please insert amount"
                    }
                })} defaultValue={1}
                />

                <FormHelperText>
                    {errors.amount?.message}
                </FormHelperText> <br />

                <TextField type={"number"} name="price" label="Price" fullWidth {...register("price", {
                    required: {
                        value: true,
                        message: "Please enter coupon price"
                    },
                })}
                />
                <FormHelperText>
                    {errors.price?.message}
                </FormHelperText> <br />

                <TextField type={"text"} name="image" label="Image URL" fullWidth {...register("image", {
                    required: {
                        value: true,
                        message: "Please enter Image URL"
                    },
                })}
                />

                <FormHelperText>
                    {errors.image?.message}
                </FormHelperText> <br />

                <Button type="submit" variant="contained" fullWidth>Add Coupon</Button>
            </Box>
        </div>
    );
}


export default AddCoupon;
