import { Notyf } from "notyf";

export enum SuccessMessage {
    LOGIN_APPROVED = "Successful login",
    LOGOUT_APPROVED = "Logged out",
    ADD_SUCCESSFUL = " was added succesfully",
    UPDATE_SUCCESSFUL = " was updated successfully",
    DELETE_SUCCESSFUL = " was deleted successfully",
    PURCHASE_SUCCESSFUL = "Coupon was purchased successfully",
}

export enum ErrorMessage {
    TOKEN_ERROR = "Please login first",
    LOGIN_FAILED = "Invalid login, please try again",
    ADD_FAILED = "Failed to add ",
    UPDATE_FAILED = "Failed to update ",
    DELETE_FAILED = "Failed to delete ",
    COUPON_OUT_OF_STOCK = "We're sorry, this coupon has ran out of stock",
    COUPON_ALREADY_OWNED = "We're sorry but you already own this coupon"
}

class Notify {
    private notification = new Notyf({ duration: 4000, position: { x: "center", y: "top" } });

    public success(message: string) {
        this.notification.success(message);
    }

    public error(message: string) {
        const errorMessage = this.extractMessage(message);
        this.notification.error(errorMessage);
    }

    private extractMessage(err: any): string {
        if (typeof err === "string") {
            return err;
        }
        if (typeof err?.response?.data === "string") {
            return err.response.data;
        }
        if (Array.isArray(err?.response?.data)) {
            return err.response.data[0];
        }
        if (typeof err?.message === "string") {
            return err.message;
        }
        return "Something went wrong, check code";
    }

}

const notify = new Notify();

export default notify;