import Coupon from './../modal/coupon';

export class GuestState {
    coupons: Coupon[] = [];
}

export enum GuestActionType {
    DOWNLOAD_GUEST_COUPONS = "Download Guest Coupons",
    UPDATE_COUPON_AMOUNT = "Update Coupon Amount",
    CLEAR_GUEST_COUPONS = "Clear Guest Coupons",
}

export interface GuestAction {
    type: GuestActionType;
    payload?: any;
}

export function downloadGuestCoupons(coupons: Coupon[]): GuestAction {
    return { type: GuestActionType.DOWNLOAD_GUEST_COUPONS, payload: coupons };
}

export function updateCouponAmount(coupon: Coupon): GuestAction {
    return { type: GuestActionType.UPDATE_COUPON_AMOUNT, payload: coupon };
}

export function clearGuestCoupons(): GuestAction {
    return { type: GuestActionType.CLEAR_GUEST_COUPONS }
}

export function guestReducer(currentState: GuestState = new GuestState(), action: GuestAction): GuestState {
    let newState = { ...currentState };

    switch (action.type) {
        case GuestActionType.DOWNLOAD_GUEST_COUPONS:
            newState.coupons = action.payload;
            break;

        case GuestActionType.UPDATE_COUPON_AMOUNT:
            newState.coupons = newState.coupons.filter(item => item.id != action.payload.id);
            newState.coupons.push(action.payload);
            break;

        case GuestActionType.CLEAR_GUEST_COUPONS:
            newState.coupons = [];
            break;
    }
    return newState;
}