import "./getAllCompanyCoupons.css";
import { useState } from 'react';
import Coupon from './../../../modal/coupon';
import { useEffect } from 'react';
import { store } from './../../../redux/store';
import { useLocation } from 'react-router-dom';
import SingleCoupon from "../singleCoupon/singleCoupon";
import notify, { ErrorMessage } from './../../../utils/notify';
import { useNavigate } from 'react-router-dom';
import { FormControl, InputLabel, Select, MenuItem, SelectChangeEvent, Slider, Button, Typography } from "@mui/material";

function GetAllCompanyCoupons(): JSX.Element {
    const [coupons, setCoupons] = useState<Coupon[]>([]);
    const [category, setCategory] = useState("");
    const [maxPrice, setMaxPrice] = useState(1000);

    const navigate = useNavigate();
    const location = useLocation();
    const { id } = location.state as any;

    const handleReset = () => {
        setCategory("");
        setMaxPrice(1000);
    };

    const handleSelectChange = (event: SelectChangeEvent) => {
        setCategory(event.target.value as string);
    }

    const handleSliderChange = (event: Event, newValue: number) => {
        setMaxPrice(newValue as number);
    };

    useEffect(() => {
        switch (store.getState().authState.clientType) {
            case "ADMIN":
                const company = store.getState().companyState.companies.find((item) => item.id == id);
                setCoupons(company.coupons);
                break;
            case "COMPANY":
                setCoupons(store.getState().couponState.coupons);
                break;
            default:
                notify.error(ErrorMessage.TOKEN_ERROR);
                navigate("/login");
        }
    });

    return (
        <div className="getAllCompanyCoupons">
            <h1>Company Coupons</h1>
            <form className="solidBox" onReset={handleReset}>
                <Typography>Filters</Typography> <hr />
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Category</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={category}
                        defaultValue="FOOD"
                        label="Category"
                        onChange={handleSelectChange}>
                        <MenuItem value={"FOOD"}>FOOD</MenuItem>
                        <MenuItem value={"ELECTRICITY"}>ELECTRICITY</MenuItem>
                        <MenuItem value={"RESTAURANT"}>RESTAURANT</MenuItem>
                        <MenuItem value={"VACATION"}>VACATION</MenuItem>
                    </Select>
                </FormControl> <br /> <br />
                <Typography gutterBottom>Price</Typography>
                <Slider defaultValue={1000} max={1000} aria-label="Price" valueLabelDisplay="auto" onChange={handleSliderChange} />
                <Button type={"reset"} variant="contained">Reset</Button>
            </form>
            <hr />
            {coupons
                .filter(item => item.category.includes(category))
                .filter(item => item.price <= maxPrice)
                .map(item => <SingleCoupon key={item.id} couponData={item} />)}
        </div>
    );
}

export default GetAllCompanyCoupons;
