import Coupon from "../modal/coupon";

export class CouponState {
    coupons: Coupon[] = [];
}

export enum CouponActionType {
    ADD_COUPON = "Add Coupon",
    UPDATE_COUPON = "Update Coupon",
    DELETE_COUPON = "Delete Coupon",
    DOWNLOAD_COUPONS = "Download Coupons",
    CLEAR_COUPONS = "Clear Coupons",
}

export interface CouponAction {
    type: CouponActionType;
    payload?: any;
}

export function addCoupon(coupon: Coupon): CouponAction {
    return { type: CouponActionType.ADD_COUPON, payload: coupon };
}

export function updateCoupon(coupon: Coupon): CouponAction {
    return { type: CouponActionType.UPDATE_COUPON, payload: coupon };
}

export function deleteCoupon(couponID: number): CouponAction {
    return { type: CouponActionType.DELETE_COUPON, payload: couponID };
}

export function downloadCoupons(coupons: Coupon[]): CouponAction {
    return { type: CouponActionType.DOWNLOAD_COUPONS, payload: coupons };
}

export function clearCoupons(): CouponAction {
    return { type: CouponActionType.CLEAR_COUPONS };
}

export function couponReducer(currentState: CouponState = new CouponState(), action: CouponAction): CouponState {
    let newState = { ...currentState };

    switch (action.type) {
        case CouponActionType.ADD_COUPON:
            const newCoupons = [...newState.coupons];
            newCoupons.push(action.payload);
            newState.coupons = newCoupons;
            break;

        case CouponActionType.UPDATE_COUPON:
            newState.coupons = newState.coupons.filter(item => item.id != action.payload.id);
            newState.coupons.push(action.payload);
            break;

        case CouponActionType.DELETE_COUPON:
            newState.coupons = newState.coupons.filter(item => item.id != action.payload);
            break;

        case CouponActionType.DOWNLOAD_COUPONS:
            newState.coupons = action.payload;
            break;

        case CouponActionType.CLEAR_COUPONS:
            newState.coupons = [];
            break;
    }
    return newState;
}