import { useForm } from 'react-hook-form';
import Company from './../../../modal/company';
import { Button, TextField } from "@mui/material";
import { useState } from 'react';
import { useEffect } from 'react';
import { store } from "../../../redux/store";
import { useNavigate } from 'react-router-dom';
import notify, { ErrorMessage, SuccessMessage } from "../../../utils/notify";
import { useLocation } from 'react-router-dom';
import jwtAxios from './../../../utils/jwtAxios';
import globals from './../../../utils/global';
import { updateCompany } from './../../../redux/companyState';
import "./updateCompany.css";

function UpdateCompany(): JSX.Element {
    const { handleSubmit } = useForm<Company>();
    const [company, setCompany] = useState<Company>(new Company());
    const navigate = useNavigate();
    const location = useLocation();
    const { id } = location.state as any;

    useEffect(() => {
        if (store.getState().authState.clientType !== "ADMIN") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
        } else {
            setCompany(store.getState().companyState.companies.find(item => item.id == id));
        }
    }, []);

    const handleChange = (args: any) => {
        const { name, value } = args.target;
        setCompany({
            ...company,
            [name]: value,
        });
    }

    const send = () => {
        jwtAxios.put(globals.adminURLs.updateCompany, company)
            .then((response) => {
                if (response.status == 200) {
                    store.dispatch(updateCompany(company));
                    notify.success("Company" + SuccessMessage.UPDATE_SUCCESSFUL);
                    navigate("/homePage");
                }
            })
            .catch((err) => {
                console.log(err);
                notify.error(ErrorMessage.UPDATE_FAILED + "company");
            })
    };

    return (
        <div className="updateCompany">
            <h1>Update Company</h1> <hr />
            <form className="solidBox" onSubmit={handleSubmit(send)}>
                <TextField type={"text"} name="name" label="Name" variant="outlined" fullWidth value={company.name} disabled />
                <br /> <br />
                <TextField type={"email"} name="email" label="Email" variant="outlined" fullWidth onChange={handleChange} value={company.email} required />
                <br /> <br />
                <Button type="submit" color="primary" variant="contained" fullWidth>Update Company</Button>
            </form>
        </div>
    );
}

export default UpdateCompany;
