import { useForm } from 'react-hook-form';
import Customer from './../../../modal/customer';
import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import { store } from "../../../redux/store";
import notify, { ErrorMessage, SuccessMessage } from "../../../utils/notify";
import { Button, FormHelperText, TextField } from "@mui/material";
import jwtAxios from './../../../utils/jwtAxios';
import globals from './../../../utils/global';
import { addCustomer } from "../../../redux/customerState";
import "./addCustomer.css";

function AddCustomer(): JSX.Element {
    const { register, handleSubmit, formState: { errors } } = useForm<Customer>();
    const navigate = useNavigate();

    useEffect(() => {
        if (store.getState().authState.clientType !== "ADMIN") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
        }
    }, []);

    const send = (customer: Customer) => {
        jwtAxios.post(globals.adminURLs.addCustomer, customer)
            .then((response) => {
                if (response.status == 200) {
                    customer.id = response.data;
                }
            })
            .then(() => {
                store.dispatch(addCustomer(customer));
                notify.success("Customer" + SuccessMessage.ADD_SUCCESSFUL);
                navigate("/homePage");
            })
            .catch((err) => {
                console.log(err);
                notify.error(ErrorMessage.ADD_FAILED + "customer");
            })
    };

    return (
        <div className="addCustomer">
            <h1>Add Customer</h1> <hr />
            <form className="solidBox" onSubmit={handleSubmit(send)}>
                <TextField type={"text"} name="firstName" label="First Name" variant="outlined" fullWidth {...register("firstName", {
                    required: {
                        value: true,
                        message: "Please enter first name",
                    },
                })}
                />
                <FormHelperText>
                    {errors.firstName?.message}
                </FormHelperText> <br />

                <TextField type={"text"} name="lastName" label="Last Name" variant="outlined" fullWidth {...register("lastName", {
                    required: {
                        value: true,
                        message: "Please enter last name",
                    },
                })}
                />
                <FormHelperText>
                    {errors.lastName?.message}
                </FormHelperText> <br />
                <TextField type={"email"} name="email" label="Email" variant="outlined" fullWidth {...register("email", {
                    required: {
                        value: true,
                        message: "Please enter a valid email",
                    },
                })}
                />
                <FormHelperText>
                    {errors.email?.message}
                </FormHelperText> <br />

                <TextField type={"password"} name="password" label="Password" variant="outlined" fullWidth {...register("password", {
                    required: {
                        value: true,
                        message: "Please enter password",
                    },
                })}
                />
                <FormHelperText>
                    {errors.password?.message}
                </FormHelperText> <br />

                <Button type="submit" variant="contained" color="primary" fullWidth>Add Customer</Button>
            </form>
        </div>
    );
}

export default AddCustomer;
