import "./getCustomerDetails.css";
import { useEffect, useState } from 'react';
import Customer from "../../../modal/customer";
import { useNavigate } from 'react-router-dom';
import { store } from "../../../redux/store";
import notify, { ErrorMessage } from "../../../utils/notify";
import SingleCustomer from './../singleCustomer/singleCustomer';

function GetCustomerDetails(): JSX.Element {
    const [customer, setCustomer] = useState<Customer>(new Customer());
    const navigate = useNavigate();

    useEffect(() => {
        if (store.getState().authState.clientType !== "CUSTOMER") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
        } else {
            setCustomer(store.getState().customerState.customers[0]);
        }
    }, []);

    return (
        <div className="getCustomerDetails">
            <h1>Customer Details</h1> <hr />

            {<SingleCustomer key={customer.id} customerData={customer} />}
        </div>
    );
}

export default GetCustomerDetails;
