import { SyntheticEvent, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Company from "../../../modal/company";
import notify, { ErrorMessage } from "../../../utils/notify";
import SingleCompany from "../../company/singleCompany/singleCompany";
import { store } from './../../../redux/store';
import { Button, TextField } from "@mui/material";
import "./getAllCompanies.css";

function GetAllCompanies(): JSX.Element {
    const [companies, setCompanies] = useState<Company[]>([]);
    const navigate = useNavigate();
    const [search, setSearch] = useState("");

    useEffect(() => {
        if (store.getState().authState.clientType !== "ADMIN") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
        } else {
            setCompanies(store.getState().companyState.companies);
        }
    }, []);

    const handleSearch = (args: SyntheticEvent) => {
        const value = (args.target as HTMLInputElement).value;
        setSearch(value);
    }

    return (
        <div className="getAllCompanies">
            <h1>All Companies</h1>
            <form onReset={() => { setSearch("") }}>
                <TextField placeholder="Search company" onChange={handleSearch} /> &nbsp;
                <Button type="reset" variant="contained">Clear</Button>
            </form> <hr />
            {companies.filter(item =>
                item.name.includes(search) ||
                item.name.toLowerCase().includes(search) ||
                item.email.toLowerCase().includes(search))
                .map((item) => <SingleCompany key={item.id} companyData={item} />)}
        </div>
    );
}

export default GetAllCompanies;
