import { TextField, Button, FormHelperText } from "@mui/material";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import Company from "../../../modal/company";
import globals from "../../../utils/global";
import jwtAxios from "../../../utils/jwtAxios";
import notify, { ErrorMessage, SuccessMessage } from "../../../utils/notify";
import { useEffect } from 'react';
import { store } from "../../../redux/store";
import { addCompany } from "../../../redux/companyState";
import "./addCompany.css";

function AddCompany(): JSX.Element {
    const { register, handleSubmit, formState: { errors } } = useForm<Company>();
    const navigate = useNavigate();

    useEffect(() => {
        if (store.getState().authState.clientType !== "ADMIN") {
            notify.error(ErrorMessage.TOKEN_ERROR);
            navigate("/login");
        }
    }, []);

    const send = (company: Company) => {
        jwtAxios.post(globals.adminURLs.addCompany, company)
            .then((response) => {
                if (response.status == 200) {
                    company.id = response.data;
                }
            })
            .then(() => {
                store.dispatch(addCompany(company));
                notify.success("Company" + SuccessMessage.ADD_SUCCESSFUL);
                navigate("/homePage");
            })
            .catch((err) => {
                console.log(err);
                notify.error(ErrorMessage.ADD_FAILED + "company");
            })
    };

    return (
        <div className="addCompany">
            <h1>Add Company</h1> <hr />
            <form className="solidBox" onSubmit={handleSubmit(send)}>
                <TextField type={"text"} name="name" label="Name" variant="outlined" fullWidth {...register("name", {
                    required: {
                        value: true,
                        message: "Please enter company name",
                    },
                })}
                />
                <FormHelperText>
                    {errors.name?.message}
                </FormHelperText> <br />

                <TextField type={"email"} name="email" label="Email" variant="outlined" fullWidth {...register("email", {
                    required: {
                        value: true,
                        message: "Please enter a valid email",
                    },
                })}
                />
                <FormHelperText>
                    {errors.email?.message}
                </FormHelperText> <br />

                <TextField type={"password"} name="password" label="Password" variant="outlined" fullWidth {...register("password", {
                    required: {
                        value: true,
                        message: "Please enter password",
                    },
                })}
                />
                <FormHelperText>
                    {errors.password?.message}
                </FormHelperText> <br />

                <Button type="submit" variant="contained" color="primary" fullWidth>Add Company</Button>
            </form>
        </div>
    );
}

export default AddCompany;
