import { AppBar, Box, Button, ButtonGroup, IconButton, Menu, MenuItem, Toolbar, Typography } from "@mui/material";
import HomeIcon from '@mui/icons-material/Home';
import MenuIcon from '@mui/icons-material/Menu';
import DarkModeOutlinedIcon from '@mui/icons-material/DarkModeOutlined';
import { Brightness5Outlined } from "@mui/icons-material";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { UserLogout } from "../../../../redux/authState";
import { store } from "../../../../redux/store";
import notify, { SuccessMessage } from "../../../../utils/notify";
import { clearCompanies } from "../../../../redux/companyState";
import { clearCoupons } from "../../../../redux/couponState";
import { clearCustomers } from "../../../../redux/customerState";
import "./myHeader.css";
import { clearGuestCoupons } from "../../../../redux/guestState";

function MyHeader({ setDark, setBright }: any): JSX.Element {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const [menuOptions, setMenuOptions] = useState([]);
    const [menuLinks, setMenuLinks] = useState([]);
    const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);

    const adminOptions = ["Add Company", "All Companies", "Add Customer", "All Customers",];
    const adminLinks = ["/admin/addCompany", "/admin/getAllCompanies", "/admin/addCustomer", "/admin/getAllCustomers",];

    const companyOptions = ["Add Coupon", "All Coupons", "Details",];
    const companyLinks = ["/company/addCoupon", "/company/getAllCompanyCoupons", "/company/getCompanyDetails",];

    const customerOptions = ["Purchase Coupons", "My Coupons", "Details",];
    const customerLinks = ["/", "/customer/getCustomerCoupons", "/customer/getCustomerDetails",];

    const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
        switch (store.getState().authState.clientType) {
            case "ADMIN":
                setMenuOptions(adminOptions);
                setMenuLinks(adminLinks);
                break;
            case "COMPANY":
                setMenuOptions(companyOptions);
                setMenuLinks(companyLinks);
                break;
            case "CUSTOMER":
                setMenuOptions(customerOptions);
                setMenuLinks(customerLinks);
                break;
            default:
                setMenuOptions([]);
                setMenuLinks([]);
        }
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const home = () => {
        if (store.getState().authState.clientType == "") {
            navigate("/")
        } else {
            navigate("/homePage");
        }
    };

    const login = () => {
        navigate("/login");
    };

    const logout = () => {
        switch (store.getState().authState.clientType) {
            case "ADMIN":
                dispatch(clearCompanies());
                dispatch(clearCustomers());
                break;

            case "COMPANY":
                dispatch(clearCompanies());
                dispatch(clearCoupons());
                break;

            case "CUSTOMER":
                dispatch(clearCustomers());
                break;
        }
        dispatch(clearGuestCoupons());
        dispatch(UserLogout());
        notify.success(SuccessMessage.LOGOUT_APPROVED);
        navigate("/")
    };

    return (
        <div className="myHeader">
            <Box sx={{ flexGrow: 1 }}>
                <AppBar position="static">
                    <Toolbar>
                        {store.getState().authState.clientType != "" &&
                            <IconButton
                                size="large"
                                edge="start"
                                color="inherit"
                                aria-label="menu"
                                aria-controls="menu-appbar"
                                sx={{ mr: 2 }}
                                onClick={handleMenu}
                            >
                                <MenuIcon />
                            </IconButton>}
                        {store.getState().authState.clientType != "" &&
                            <Menu
                                id="menu-appbar"
                                anchorEl={anchorEl}
                                open={Boolean(anchorEl)}
                                onClose={handleClose}>
                                {menuOptions.map((option, index) => (
                                    <MenuItem
                                        key={index}
                                        onClick={() => {
                                            navigate(menuLinks[index], { state: { id: 0 } });
                                            handleClose();
                                        }}>
                                        {option}
                                    </MenuItem>
                                ))}
                            </Menu>}

                        <IconButton
                            color="inherit"
                            onClick={home}>
                            <HomeIcon />
                        </IconButton>

                        <Typography variant="h5" component="div" sx={{ flexGrow: 1 }}>
                            Coupon System
                        </Typography>
                        <ButtonGroup variant="outlined">
                            <IconButton onClick={setBright}>
                                <Brightness5Outlined />
                            </IconButton>
                            <IconButton onClick={setDark}>
                                <DarkModeOutlinedIcon />
                            </IconButton>
                        </ButtonGroup>
                        {store.getState().authState.clientType == "" ?
                            <Button color="inherit" onClick={login}>Login</Button> :
                            <Button color="inherit" onClick={logout}>Logout</Button>}
                    </Toolbar>
                </AppBar>
            </Box>
        </div>
    );
}

export default MyHeader;
